import React from 'react';
import { Helper } from '../../utils/export/exportHelpers';
import { OverviewIconStyles } from '../../utils/export/exportStyles';
import { media } from '../../utils/export/exportMedia';
import labels from '../labels/Labels.json';

const initialStyles = {
    overviewIconContainer: OverviewIconStyles.overviewIconContainer,
    iconImage: OverviewIconStyles.iconImage,
    overviewTitle: OverviewIconStyles.overviewTitle,
    overviewDesc: OverviewIconStyles.overviewDesc
};

const hoverStyles = {
    overviewIconContainer: OverviewIconStyles.overviewIconContainer,
    iconImage: Helper.overrideCSS(OverviewIconStyles.iconImage, OverviewIconStyles.iconImageHover),
    overviewTitle: OverviewIconStyles.overviewTitle,
    overviewDesc: Helper.overrideCSS(OverviewIconStyles.overviewDesc, OverviewIconStyles.overviewDescHover)
};

export default class OverviewIcon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            styles: initialStyles
        }
        this.getIconInfo = this.getIconInfo.bind(this);
    }

    getIconInfo(icon) {
        let iconInfo = { src: null, title: null, desc: null };
        if (icon) {
            iconInfo = {
                src: media[icon],
                title: labels[`${icon}OverviewTitle`],
                desc: labels[`${icon}OverviewDesc`]
            };
        }
        return iconInfo;
    }
    
    render() {
        const iconInfo = this.getIconInfo(this.props.icon);
        const styles = this.state.styles;
        return (
            <div className={styles.overviewIconContainer}>
                <a href={process.env.REACT_APP_INTERESTS_PATH}>
                    <img className={styles.iconImage}
                         src={iconInfo.src} 
                         onMouseOver={() => this.setState({ styles: hoverStyles })} 
                         onMouseOut={() => this.setState({ styles: initialStyles })} 
                    />
                </a>
                <text className={styles.overviewTitle}>{iconInfo.title}</text>
                <text className={styles.overviewDesc}>{iconInfo.desc}</text>
            </div>
        );
    }
};


