import React from 'react';
import { media } from '../../utils/export/exportMedia';
import { ImageCollageStyles } from '../../utils/export/exportStyles';

export default class ImageCollage extends React.Component {
    render() {
        return(
            <div>
                <img src={media.overviewcont_1}
                     className={ImageCollageStyles.iconImg_1} 
                />
                <img src={media.overviewcontmain}
                     className={ImageCollageStyles.iconImgMain} 
                />
                <img src={media.overviewcont_2}
                     className={ImageCollageStyles.iconImg_2} 
                />
            </div>
        );
    }
}


